.github-repositories {
    display: flex;
    justify-content: space-between;
    flex-flow: row;
    padding: $padding;
    position: absolute;
    bottom: 0;
    width: 100%;
    overflow-x: scroll;
    box-sizing: border-box;

    .repo {
        flex: 0 0 350px;
        background-color: $quaternyColor;
        border-radius: $borderRadius;
        padding: $padding;
        margin: calc(#{$margin} / 2);
        box-sizing: border-box;

        a {
            text-decoration: none;
            color: #000;
            font-weight: bold;
            font-size: 1.3em
        }
    }
}
